import React from 'react';
import { useState } from 'react';



const HookTimeChallange=()=>{
    const nTime= new Date().toLocaleTimeString();
    let [Ctime, State]= useState(nTime);
    const UpdatedTime=()=>{
        const NewTime= new Date().toLocaleTimeString();
        return State(NewTime);
    }
    return(
        <>
        <h1>{Ctime}</h1>
        <button onClick={UpdatedTime}>Click me to update time</button>
        </>
    )
}

export default HookTimeChallange;