import React from 'react';

const backgrounDiv={
    color:'Black'
};
function TopNetflixserieswithDetail(props){
    return(
        
        <>
        <div style={backgrounDiv}>
        <p>{props.title}</p>
        <img src={props.imagesrc} alt="netflix1"/>
        <a href={props.originalLink} target="_blank">
            <button type="submit" value="submit">submit</button>
        </a>
        </div>
        </>
    )
}
export default TopNetflixserieswithDetail;