import React from 'react';

const headingcss={
  
}
let timeZone="";
let today = new Date();
today= today.getHours();

if(today>=1 && today<12){
 
  timeZone="Good Morning";
  headingcss.color="green"
}else if(today>=12 && today<19){
  timeZone="Good Afternoon";
  headingcss.color="Orange"
}else{
  timeZone="Good night";
  headingcss.color="Black"
}


function Heading(){
    return <h1>Hello sir, <span style={headingcss}>{timeZone}</span></h1>
}
export default Heading;