import React from 'react';
import { useState } from 'react';





const Hooksprogram= ()=>{
    const Hookstate= useState(0);
    const [count, state]= Hookstate; 

    const increment=()=>{
        return state(count+1)
    }
    return(
        <>
        <h1>{count}</h1>
        <button onClick={increment}>click me</button>
        </>
    )
}


export default Hooksprogram;