import React from 'react';
import { useState } from 'react';






const DigitalTimeClock=()=>{

    let time= new Date().toLocaleTimeString();
    let [lTime, state]=useState(time);

    setInterval(() => {
        const Ntime= new Date().toLocaleTimeString();
        state(Ntime)
    }, 1000);

    return(
        <h1>{lTime}</h1>
    )
}

export default DigitalTimeClock;